
.BackHighlight {
    -webkit-animation: BackHighlight 1 1s ease;
            animation: BackHighlight 1 1s ease;
}

.BalHighlight {
    -webkit-animation: BalHighlight 1 .5s linear;
            animation: BalHighlight 1 .5s linear;
}

@-webkit-keyframes BackHighlight {
    0% { background-color: white; }
    50% { background-color: yellow; }
    100% { background-color: white; }
}

@keyframes BackHighlight {
    0% { background-color: white; }
    50% { background-color: yellow; }
    100% { background-color: white; }
}

@-webkit-keyframes BalHighlight {
    0% { -webkit-transform: scale(1.0, 1.0); transform: scale(1.0, 1.0); }
    25% { -webkit-transform: scale(1.0, 1.2); transform: scale(1.0, 1.2); }
    50% { -webkit-transform: scale(1.0, 1.0); transform: scale(1.0, 1.0); }
    75% { -webkit-transform: scale(1.0, 1.2); transform: scale(1.0, 1.2); }
    100% { -webkit-transform: scale(1.0, 1.0); transform: scale(1.0, 1.0); }
}

@keyframes BalHighlight {
    0% { -webkit-transform: scale(1.0, 1.0); transform: scale(1.0, 1.0); }
    25% { -webkit-transform: scale(1.0, 1.2); transform: scale(1.0, 1.2); }
    50% { -webkit-transform: scale(1.0, 1.0); transform: scale(1.0, 1.0); }
    75% { -webkit-transform: scale(1.0, 1.2); transform: scale(1.0, 1.2); }
    100% { -webkit-transform: scale(1.0, 1.0); transform: scale(1.0, 1.0); }
}



